<template>
  <div id="courseware">
    <head-top title="课件"></head-top>
    <van-pull-refresh v-model="loading" @refresh="getDataList" class="pull_fresh">
      <div class="main">
        <van-cell v-for="(item, index) in dataList" :key="index" arrow-direction="down" is-link
                  @click="showPopup(index)">
          <template slot="title">
            <div class="item">
              <div>
                <i class="iconfont icon-word"></i>
                <span class="title time">
                  {{item["MaterialName"] + "." + item["MaterialFileExt"]}}
                </span>
              </div>
              <div class="size time">
                <span v-if="item['MaterialFileSize'] > 1024 * 1024">
                  {{Math.round(item["MaterialFileSize"] / 1024.0 / 1024)}} MB
                </span>
                <span v-else>
                  {{ Math.round(item["MaterialFileSize"] / 1024) }} KB
                </span>
              </div>
            </div>
          </template>
        </van-cell>

        <van-loading v-if="loading" type="spinner" color="#1989fa">加载中...</van-loading>
        <r-lack-data v-else-if="!dataList || dataList.length === 0"></r-lack-data>
      </div>
    </van-pull-refresh>

    <van-popup v-model="show" position="bottom">
      <van-cell value="预览" @click.stop.prevent="previewFile" v-if="viewVisible">
        <van-icon slot="right-icon" class-prefix="iconfont icon-yulan"></van-icon>
      </van-cell>
      <van-cell value="下载" @click.stop.prevent="download">
        <van-icon slot="right-icon" class-prefix="iconfont icon-xiazai"></van-icon>
      </van-cell>
    </van-popup>
  </div>
</template>

<script>
// import { Dialog } from "vant";
import { FuncType, sendToAPP } from "@/utils/interFlow";

export default {
  name: "",
  // components: { [Dialog.name]: Dialog },
  props: {
    routeType: {
      type: String,
      default: "course"
    }
  },
  data () {
    return {
      dataList: [],
      show: false,
      selectIndex: -1,
      viewVisible: false,
      canOpenFileExt: ["txt", "bmp", "gif", "jpg", "png", "ico"], //, "mp4"
      loading: false
    };
  },
  created () {
  },
  mounted () {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    showPopup (index) {
      this.selectIndex = index;
      const fileInfo = this.dataList[this.selectIndex];
      this.viewVisible =
        fileInfo["MaterialLowStreamEstate"] === "2" ||
        this.canOpenFileExt.indexOf(fileInfo["MaterialFileExt"]) > -1;
      this.show = true;
    },
    getDataList () {
      this.dataList = [];
      this.loading = true;
      this.$mainHttp.courseware
        .getList({
          chID: this.$store.getters.course["CHID"],
          classID: this.$store.getters.classID
          // attribute: 2
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    download () {
      let item = this.dataList[this.selectIndex];
      let fileUrl = this.$mainHttp.common.file(item["MaterialID"]);
      if (!window.webkit) {
        window.location.href = fileUrl;
        this.show = false;
        this.$toast("下载开始");
      } else {
        this.show = false;
        sendToAPP(FuncType.downloadFile, fileUrl)
          .then(res => {
          })
          .catch(err => {
          });
      }
    },
    previewFile () {
      let item = this.dataList[this.selectIndex];
      this.toPreviewUrl(item);
      // if (item["MaterialFileSize"] > 1024 * 1024 * 15) {
      //   Dialog.confirm({
      //     title: "提示",
      //     message: "超过15MB的文档，建议您下载到本地查看",
      //     confirmButtonText: "关闭",
      //     cancelButtonText: "下载",
      //     cancelButtonColor: "#909399"
      //   })
      //     .then(() => {
      //       // on confirm
      //     })
      //     .catch(() => {
      //       this.download(item);
      //       this.show = false;
      //     });
      // } else {
      //   this.toPreviewUrl(item);
      // }
    },
    toPreviewUrl (item) {
      if (this.routeType === "course") {
        this.$router.push(
          `/coursewareView/${item["MaterialID"]}/${item["MaterialFileExt"]}/${item["MaterialName"]}`
        );
      } else {
        this.$router.push(
          `/history/coursewareView/${item["MaterialID"]}/${item["MaterialFileExt"]}/${item["MaterialName"]}`
        );
      }
    }
  },
  watch: {
    "$store.getters.courseWareState" (n, o) {
      this.getDataList();
    }
  }
};
</script>

<style lang="less" scoped>
@import url("./../styles/common/base.less");

#courseware {
  width: 100%;
  height: 100%;

  .main {
    width: 100%;
    background: #fff;
    max-height: calc(100vh - 45px);
    overflow-y: auto;

    .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-right: 2vw;
      word-break: break-all;

      .icon-word {
        color: #409eff;
      }

      .size {
        text-align: right;
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 45px);
    }
  }

  .iconfont {
    font-size: @font-sub-title;
  }

  .icon-yulan {
    padding-left: 1vw;
    color: #07c160;
  }

  .icon-xiazai {
    padding-left: 1vw;
    color: #ff976a;
  }
}
</style>



